import config from '@/services/config';
import axios from 'axios';

const servicio = {};
const url = config.url

servicio.login = (usuario, password) => {
    console.log(url,"  ",usuario," ",password);
    
    return axios.post(`${url}/seguridad/login`, {usuario, password})
        .then(r => {
            return r.data
        })
        .catch(error => {
            console.error(error)
        })
}
export default servicio