import Vue from 'vue'
import VueRouter from 'vue-router'

import Inicio from '@/views/Inicio'
import Clientes from '@/views/Clientes'
import Login from '@/views/Login'


Vue.use(VueRouter)

const routes = [
    {
        path:'/default',
        component:Inicio
    },
    {
        path:'/Login',
        component:Login
    },
    {
        path:'/Clientes',
        component:Clientes
    }
]


const router = new VueRouter({
    routes,    
	scrollBehavior(to){
		if (to.hash) {
			return {
				selector: to.hash
			}
		}
	}
});

export default router