<template>
    <div class="intro-2 py-5">
        <div class="">
            <div class="row d-flex justify-content-center">
                <div class="col-md-4 col-sm-6 col-md-offset-4 col-sm-offset-3 card-body" >
                    <form @submit="acceder">
                        <div data-background="color" data-color="blue" class="card">
                            <div class="card-header"><h3 class="card-title">Login</h3></div> 
                                <div class="card-content">
                                    <div class="form-group">
                                        <label>Usuario</label> 
                                        <input  type="text" 
                                                placeholder="Usuario" 
                                                class="form-control input-no-border" 
                                                v-model="usuario">
                                    </div> 
                                    <div class="form-group">
                                        <label>Contraseña</label> 
                                        <input  type="password" 
                                                placeholder="Contraseña" 
                                                class="form-control input-no-border" 
                                                v-model="password">
                                    </div>
                                </div> 
                                <div class="card-footer text-center">
                                    <button type="submit" class="btn btn-fill btn-block btn-azul-2h">Acceder</button> 
                                </div>
                            </div>
                    </form>
                </div>
            </div>  
            <div class="footer">
                Derechos reservados Concretos2H
            </div>
        </div>
        <!-- </div> -->
    </div>
</template>
<script>
import servicio from '@/services/servicio-base'
// import swal from '@/plugins/swal'
export default {
    data(){
        return {
            usuario:"",
            password:""
        }
    },
    methods:{
        acceder(){
            servicio.login(this.usuario,this.password)
            .then(r=> {
                console.log(r);
                if(r.value){
                    window.localStorage.token = r.data.accessToken;
                    window.localStorage.expiresIn = r.data.expiresIn;
                    window.localStorage.refreshToken = r.data.refreshToken;
                    this.$swal.fire({
                        text: r.message,
                        icon: "success"
                    });
                    this.$router.push("default")
                }else{
                    this.$swal.fire({
                        text: r.message,
                        icon: "warning"
                    })
                }                
            })
        }
    }
}
</script>
<style>
    .card {
        background-color: rgba(255, 255, 255, 0.70);
    }
    body {
        background: url("https://concretos2h.com.mx/img/3447/821.jpg")no-repeat;
        background-size: cover;        
    }
    .btn-azul-2h{
        background-color:#1B3582;
        color:whitesmoke;
    }
    .btn-azul-2h:hover{
        background-color:rgb(27, 92, 130);
        color:whitesmoke;
    }
    .full-page{
        background-image: url("../assets/img/concretos.jpg");
         background-repeat: no-repeat;
        /* background-size: 100% 200%; */
    }
    .footer {
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        background-color: gray;
        color: white;
        text-align: center;
    }
</style>